import React from "react"
import Box from "@material-ui/core/Box"
import Paper from "@material-ui/core/Paper"
import { DatePicker } from "@material-ui/pickers"
import IconButton from "@material-ui/core/IconButton"

import KeyboardArrowLeftIcon from "@material-ui/icons/KeyboardArrowLeft"
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight"
const Datepicker = props => {
  const { date, onClickLeft, onClickRight, onDateChange, datasDisponiveis, dateParse, dateUnparse } = props

  console.log(date)

  return (
    <div>
      <Box
        component={Paper}
        className="center"
        style={{ width: "250px", margin: "20px auto", padding: "20px 0" }}
      >
      <IconButton size="medium" style={{ outline:"none"}} >
          <KeyboardArrowLeftIcon onClick={onClickLeft}/>
      </IconButton>
        <DatePicker 
        autoOk
        allowKeyboardControl
        label="Selecione uma data:"
        format="dd/MM/yyyy"
        shouldDisableDate={(date) => (
          !(datasDisponiveis.includes(dateUnparse(date)))
        )}
        value={dateParse(date)}
        onChange={onDateChange}/>
        <IconButton size="medium" style={{ outline:"none"}}>
          <KeyboardArrowRightIcon onClick={onClickRight}/>
        </IconButton>
      </Box>
    </div>
  )
}

export default Datepicker
